@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
body {
  background-color: #d3d3d3;
  font-family: Montserrat, sans-serif;
}

a {
  color: #2e2f42;
  align-items: center;
  font-size: 20px;
  line-height: 1.5;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

a:hover {
  color: #f08080;
}

a:hover:after {
  content: "❯";
  margin-left: 8px;
  font-size: 20px;
}
